<template>
   <div class="main-wrap">
      <topTabs @changeTab="getPostData" v-if="this.$route.path==='/topics'"></topTabs>
      <main :key="this.$store.state.tab">
         <router-link
            :to="{name:'topic',params:{id:item.id,loginname:item.author.loginname}}"
            v-for="(item,index1) in setPostData"
            :key="index1"
         >
            <div class="post-item">
               <span v-if="item.good ===true" class="post-tag good">精选</span>
               <span v-else-if="item.top === true" class="post-tag top">置顶</span>
               <span v-else-if="item.tab ==='share'" class="post-tag other">分享</span>
               <span v-else-if="item.tab ==='ask'" class="post-tag other">问答</span>
               <span v-else-if="item.tab ==='job'" class="post-tag other">招聘</span>
               <section>
                  <span class="author-name">{{item.author.loginname}}</span>
                  <span class="time">{{item.last_reply_at | dateFilter}}</span>
                  <h1>{{item.title}}</h1>
               </section>
               <div class="post-critics">
                  <ul>
                     <li v-for="(reply,index2) in replies[index1]" :key="index2">
                        <img :src="reply.author.avatar_url" />
                     </li>
                  </ul>
                  <div v-if="item.reply_count>6">
                     <div class="more" v-if="item.reply_count-6>99">99</div>
                     <div class="more" v-else>{{item.reply_count-6}}</div>
                  </div>
               </div>
               <img :src="item.author.avatar_url" class="cover" />
            </div>
         </router-link>
      </main>
      <slot></slot>
      <toast v-if="timeout">请求超时</toast>
   </div>
</template>

<script>
import topTabs from "./TopTabs";
import toast from "./Toast";

export default {
   name: "PostList",
   data() {
      return {
         postData: [],
         replies: [],
         timeout: false
      };
   },
   components: {
      topTabs,
      toast
   },
   methods: {
      getPostData() {
         this.$store.commit("loading", true);
         this.$axios
            .get(`https://cnodejs.org/api/v1${this.$route.path}`, {
               params: {
                  page: this.$store.state.pages,
                  limit: this.$store.state.limits,
                  tab: this.$store.state.tab
               }
            })
            .then(r => {
               let recent;
               if (this.$parent.n === true) {
                  recent = r.data.data.recent_topics;
               } else {
                  recent = r.data.data.recent_replies;
               }
               this.postData = recent || r.data.data;
               this.replies = [];
               this.getReplyData();
               this.$store.commit("loading", false);
            })
            .catch(err => {
               if (!err.response) {
                  this.timeout = true;
                  this.$store.commit("loading", false);
               } else {
                  throw new Error(err);
               }
            });
      },
      // 获取评论者的头像
      getReplyData() {
         this.setPostData.forEach((value, index) => {
            this.$axios
               .get(`https://cnodejs.org/api/v1/topic/${value.id}`)
               .then(r => {
                  // 最多取 6 条数据
                  if (r.data.data.replies.length > 6) {
                     this.replies.push(r.data.data.replies.slice(0, 6));
                  } else {
                     this.replies.push(r.data.data.replies);
                  }
               })
               .catch(err => {
                  if (!err.response) {
                     this.timeout = true;
                  } else {
                     this.$store.commit("loading", false);
                     throw new Error("请求错误" + err);
                  }
               });
         });
      },
      scroll() {
         document.addEventListener("scroll", () => {
            if (scrollY > 700) {
               if (
                  document.querySelector("#app .topTabs") &&
                  document.querySelector("#app nav")
               ) {
                  document.querySelector("#app nav").style.top = "-56px";
                  document.querySelector("#app .topTabs").style.top = 0;
               }
            } else {
               if (
                  document.querySelector("#app .topTabs") &&
                  document.querySelector("#app nav")
               ) {
                  document.querySelector("#app nav").style.top = 0;
                  document.querySelector("#app .topTabs").style.top = "56px";
               }
            }
         });
      },
      // 获取其他主题帖，最多4条
      getZeroReply() {
         this.$store.state.zeroReply = this.postData.slice(0, 4);
         sessionStorage.setItem(
            "data",
            JSON.stringify(this.$store.state.zeroReply)
         );
      },
      // 防抖函数
      debounce(fn, time) {
         const timer = null;
         return () => {
            if (timer) clearTimeout(timer);
            timer = setTimeout(() => {
               fn();
            }, time);
         };
      },
      // 上滑更新列表，默认展示20条
      addPostData() {
         this.debounce(this.getPostData(), 1000);
         let n = 1;
         document.addEventListener("scroll", () => {
            if (scrollY > 1500 * n) {
               let page = 1;
               let limit = 10 * (n + 1);
               let params = { page, limit };
               this.$store.commit("addPostData", params);
               this.debounce(this.getPostData(), 1000);
               n++;
            }
         });
      }
   },
   computed: {
      setPostData() {
         sessionStorage.setItem("postData", JSON.stringify(this.postData));
         if (Array.isArray(JSON.parse(sessionStorage.getItem("postData")))) {
            return JSON.parse(sessionStorage.getItem("postData"));
         } else {
            return [JSON.parse(sessionStorage.getItem("postData"))];
         }
      }
   },
   beforeMount() {
      this.addPostData();
      this.scroll();
   },
   updated() {
      this.getZeroReply();
   },
   watch: {
      $route(to, from) {
         this.getPostData();
      }
   }
};
</script>

