<template>
   <div
      class="login-wrap"
      v-if="this.$store.state.loginIsShow"
      @click.self="closeLogin(false)"
      @mousewheel.prevent
   >
      <main>
         <div class="title">登录</div>
         <input type="text" placeholder="输入accessToken" v-model="text" />
         <input type="submit" value="登录" @click="postData" />
      </main>
      <toast v-if="resStatus === 200">登录成功</toast>
      <toast v-else-if="noUser">用户不存在</toast>
      <toast v-else-if="timeout">登录超时</toast>
   </div>
</template>

<script>
import toast from "../components/Toast";

export default {
   data() {
      return {
         text: "",
         resStatus: "",
         timeout: false,
         noUser: false
      };
   },
   components: {
      toast
   },
   methods: {
      closeLogin(msg) {
         this.$store.commit("closeLogin", msg);
      },
      postData() {
         this.$axios
            .post("https://cnodejs.org/api/v1/accesstoken", {
               accesstoken: this.text
            })
            .then(r => {
               if (r.status === 200) {
                  this.resStatus = 200;
                  this.$store.commit("login", true);
                  sessionStorage.setItem("userData", JSON.stringify(r.data));
                  setTimeout(() => {
                     this.$store.commit("closeLogin", false);
                  }, 1500);
               }
            })
            .catch(e => {
               if (e.request.status > 300) {
                  this.noUser = true;
               } else if (!e.config) {
                  this.timeout = true;
                  console.error("登录超时");
               } else {
                  console.error(e);
               }
            });
      }
   }
};
</script>

