import Vue from 'vue'
import Router from 'vue-router'
import topNavbar from './components/TopNavbar'
import topTabs from './components/TopTabs'
import postList from './components/PostList'
import topic from './components/Topic'
import userHome from './components/UserHome'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      name: 'root',
      path: '/topics',
      components: {
        topNavbar,
        topTabs,
        postList,
      }
    },
    {
      name: '/',
      path: '/',
      redirect: '/topics'
    },
    {
      name: 'topic',
      path: '/topic:id&loginname:loginname',
      components: {
        topNavbar,
        topic,
      }
    },
    {
      name: 'user',
      path: '/user/:loginname',
      components: {
        topNavbar,
        userHome
      }
    },
    {
      name: 'recent_topics',
      path: '/user/:loginname&tab:tab1',
      components: {
        topNavbar,
        userHome
      }
    },
    {
      name: 'recent_replies',
      path: '/user/:loginname&tab:tab',
      redirect: '/user/:loginname',
      components: {
        topNavbar,
        userHome
      }
    }
  ]
})
