<template>
   <div class="topic-wrap" v-show="topicShow===true">
      <!-- 文章主体 -->
      <div class="main-wrap">
         <main>
            <!-- 文章标签 -->
            <span v-if="topicData.good === true" class="post-tag good">精选</span>
            <span v-else-if="topicData.top === true" class="post-tag top">置顶</span>
            <span v-else-if="topicData.tab ==='share'" class="post-tag other">分享</span>
            <span v-else-if="topicData.tab ==='ask'" class="post-tag other">问答</span>
            <span v-else-if="topicData.tab ==='job'" class="post-tag other">招聘</span>
            <section>
               <!-- 文章标题 -->
               <h1>{{topicData.title}}</h1>
               <small>
                  <span class="topic-time">发布于 {{topicData.create_at | dateFilter}}</span>
                  <span class="topic-author">作者 {{loginname}}</span>
                  <span class="topic-views">{{topicData.visit_count}} 次浏览</span>
                  <span class="topic-last">最后一次编辑于 {{topicData.last_reply_at | dateFilter}}</span>
               </small>
               <!-- 文章内容 -->
               <div class="topic-content" v-html="topicContent"></div>
            </section>
         </main>
         <replyList :replies="topicData.replies" :loginname="loginname"></replyList>
      </div>
      <!-- 侧边栏 -->
      <aside>
         <!-- 向 userInfor 组件传递数据 -->
         <userInfor :loginname="loginname" :authorImg="authorImg"></userInfor>
         <otherPost></otherPost>
         <qrcode></qrcode>
      </aside>
   </div>
</template>

<script>
import Vue from "vue";
import qrcode from "./Qrcode";
import userInfor from "./UserInfor";
import replyList from "./ReplyList";
import otherPost from "./OtherPost";

export default {
   data() {
      return {
         id: this.$route.params.id,
         topicData: {},
         topicContent: "",
         loginname: "",
         authorImg: "",
         topicShow: false
      };
   },
   components: {
      qrcode,
      userInfor,
      replyList,
      otherPost
   },
   methods: {
      getTopicData() {
         this.$store.commit("loading", true);
         // 获取数据并存在 data 中
         this.$axios
            .get(`https://cnodejs.org/api/v1/topic/${this.id}`)
            .then(r => {
               if (r.status === 200) {
                  this.topicData = r.data.data;
                  this.topicContent = this.topicData.content;
                  this.loginname = this.topicData.author.loginname;
                  if (this.topicData.author) {
                     this.authorImg = this.topicData.author.avatar_url;
                  }
                  this.topicShow = true;
               }
               this.$store.commit("loading", false);
            })
            .catch(err => {
               this.$store.commit("loading", false);
               throw new Error("请求错误");
            });
      },
      switchTabStyle() {
         document.querySelector("nav .wrap li").classList.remove("active");
      }
   },
   beforeMount() {
      this.getTopicData();
   },
   mounted() {
      this.switchTabStyle();
   },
   watch: {
      // 检测到路由发生变化是刷新页面
      $route(to, from) {
         window.location.reload();
      }
   }
};
</script>

