<template>
   <article class="reply-wrap" v-if="replies&&replyData">
      <div class="title">评论 · {{replies.length}}</div>
      <!-- 登录之后显示发表评论 -->
      <div class="newReply" v-if="this.$store.state.isLogin">
         <div>
            <img :src="userImg" alt />
         </div>
         <input type="text" placeholder="请输入你的评论" v-model="msg" />
         <input type="submit" @click="setNewReply" value="提交" />
      </div>
      <div class="reply-list">
         <span v-if="replies.length===0">暂无评论</span>

         <section v-for="(reply,index) in replyData" :key="index">
            <div v-if="reply">
               <img :src="reply.author.avatar_url" />
            </div>
            <div class="reply-main" v-if="reply">
               <div class="reply-top">
                  <span class="reply-name">{{reply.author.loginname}}</span>
                  <span v-if="reply.author.loginname===loginname" class="isAuthor">作者</span>
               </div>
               <div class="reply-content" v-html="reply.content"></div>
               <div class="reply-footer">
                  <span class="reply-time">{{reply.create_at | dateFilter}}</span>
                  <span class="love" v-if="reply.ups.length>0">
                     <i></i>
                     <span>{{reply.ups.length}}</span>
                  </span>
               </div>
            </div>
         </section>
         <!-- 当回复数大于5条时显示加载更多 -->
         <div
            v-if="replies.length>6"
            v-show="replyData.length!=replies.length"
            v-on:click="getReplyData"
            class="load-more"
         >加载更多</div>
      </div>
   </article>
</template>

<script>
export default {
   props: ["replies", "loginname"],
   data() {
      return {
         replyData: [],
         userImg: null,
         msg: "",
         userName: ""
      };
   },
   methods: {
      // 回复数大于5条时默认显示5条，点击加载更多增加5条
      getReplyData() {
         if (this.replies) {
            if (
               this.replies.length > this.replyData.length &&
               this.replies.length >= 6
            ) {
               for (let i = 0; i < 6; i++) {
                  this.replyData.push(this.replies[i + this.replyData.length]);
               }
            } else {
               this.replyData = this.replies;
            }
         }
      },
      setNewReply() {
         alert("不支持评论");
      }
   },
   mounted() {
      this.getReplyData();
      if (JSON.parse(sessionStorage.getItem("userData"))) {
         this.userImg = JSON.parse(
            sessionStorage.getItem("userData")
         ).avatar_url;
         this.userName = JSON.parse(
            sessionStorage.getItem("userData")
         ).loginname;
      }
   },
   beforeUpdate() {
      this.getReplyData();
   }
};
</script>

