<template>
   <!-- 导航栏 -->
   <nav>
      <div class="wrap">
         <!-- 左侧 -->
         <div class="left">
            <router-link :to="{name:'root'}">
               <img src="https://static2.cnodejs.org/public/images/cnodejs_light.svg" />
            </router-link>
            <ul>
               <li v-on:click="switchTab" class="active">
                  <router-link :to="{name:'root'}">首页</router-link>
               </li>
               <li v-on:click="switchTab">
                  <router-link to="#">新手入门</router-link>
               </li>
               <li v-on:click="switchTab">
                  <router-link to="#">API</router-link>
               </li>
               <li v-on:click="switchTab">
                  <router-link to="#">关于</router-link>
               </li>
            </ul>
         </div>
         <!-- 右侧 -->
         <div class="right">
            <div class="search">
               <img src="../assets/search-icon.svg" />
               <input type="text" placeholder="搜索cnode" v-model="inputMsg" @keyup.enter="search" />
            </div>
            <div class="log-in">
               <ul v-if="this.$store.state.isLogin===false">
                  <li @click="signUp">注册</li>
                  <li @click="showLogin(true)">登录</li>
               </ul>
               <router-link v-else :to="{name:'user',params:{loginname:userName}}">
                  <img :src="userImg" alt />
               </router-link>
            </div>
         </div>
      </div>
   </nav>
</template>

<script>
let ul = document.querySelector("nav ul");
export default {
   name: "TopNavbar",
   data() {
      return {
         active: false,
         userImg: null,
         userName: null,
         inputMsg: ""
      };
   },
   methods: {
      switchTab(e) {
         e.target.parentNode.childNodes.forEach(i =>
            i.classList.remove("active")
         );
         e.target.classList.add("active");
      },
      showLogin(msg) {
         console.log(this.$store.state.loginIsShow);
         this.$store.commit("closeLogin", msg);
      },
      signUp() {
         alert("很抱歉没有注册功能");
      },
      search() {
         //如果input内有内容就提交到谷歌搜索
         if (this.inputMsg) {
            window.open(
               `https://www.google.com.hk/search?hl=zh-CN&q=site:cnodejs.org ${
                  this.inputMsg
               }`
            );
         }
      }
   },
   updated() {
      if (this.$store.state.isLogin) {
         this.userImg = JSON.parse(
            sessionStorage.getItem("userData")
         ).avatar_url;
         this.userName = JSON.parse(
            sessionStorage.getItem("userData")
         ).loginname;
      }
   }
};
</script>

