<template>
   <div class="topTabs">
      <ul>
         <li
            v-for="(tab,index) of tabs"
            :key="index"
            v-on:click="debounce(switchTab(index,$event), 1000)"
            :class="index"
         >{{tab}}</li>
      </ul>
   </div>
</template>
<script>
export default {
   name: "TopTabs",
   data() {
      return {
         tabs: {
            all: "全部",
            good: "精华",
            share: "分享",
            ask: "问答",
            job: "招聘"
         },
         isShow: false
      };
   },
   methods: {
      switchTab(msg, e) {
         e.target.parentNode.childNodes.forEach(i =>
            i.classList.remove("active")
         );
         e.target.classList.add("active");
         this.$store.commit("changeTab", msg);
         this.$emit("changeTab");
      },
      defaultActive() {
         const tab = this.$store.state.tab;
         document.querySelector(`.${tab}`).classList.add("active");
      },
      debounce(fn, time) {
         const timer = null;
         return () => {
            if (timer) clearTimeout(timer);
            timer = setTimeout(() => {
               fn();
            }, time);
         };
      }
   },
   mounted() {
      this.defaultActive();
   }
};
</script>
