<template>
   <main class="user-wrap">
      <div class="user-infor">
         <div class="author-img">
            <router-link :to="{name:'user', params:{loginname:loginname}}">
               <img v-bind:src="authorImg" />
            </router-link>
            <div class="author-level">{{level | levelFilter}}</div>
         </div>
         <span class="author-name">{{loginname}}</span>
         <span class="signature">{{signature}}</span>
      </div>
      <div class="user-data">
         <div class="integral">
            <span class="integral-infor">{{integral}}</span>
            <span>积分</span>
         </div>
         <div class="post">
            <span class="post-infor">{{post}}</span>
            <span>主题</span>
         </div>
      </div>
   </main>
</template>

<script>
export default {
   // 接收父组件传递的数据
   data() {
      return {
         authorName: "加载中…",
         signature: "这家伙很懒，什么个性签名都没有留下",
         level: "LV1",
         integral: 0,
         post: 0,
         loginname: this.$route.params.loginname,
         authorImg: "",
         gitbub: ""
      };
   },
   methods: {
      getData() {
         this.$axios
            .get(`https://cnodejs.org/api/v1/user/${this.loginname}`)
            .then(r => {
               if (r.status === 200) {
                  this.integral = r.data.data.score;
                  this.level = r.data.data.create_at;
                  this.authorImg = r.data.data.avatar_url;
                  if (r.data.data.recent_topics && r.data.data.recent_replies) {
                     this.post =
                        r.data.data.recent_topics.length +
                        r.data.data.recent_replies.length;
                  }
               }
            })
            .catch(err => {
               throw new Error(err.status + "请求错误");
            });
      },
      getUserCardData() {
         this.$emit("input", this.data);
      }
   },
   filters: {
      levelFilter(value) {
         let createDate = new Date(value);
         let nowYear = new Date().getFullYear();
         let createYear = createDate.getFullYear();
         return `LV ${nowYear - createYear + 1}`;
      }
   },
   beforeMount() {
      this.getData();
   }
};
</script>

