import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    tab: 'all',
    zeroReply: '',
    loginIsShow: false,
    isLogin: false,
    isLoding: false,
    pages: 1,
    limits: 10
  },
  mutations: {
    changeTab(state, msg) {
      state.tab = msg;
    },
    closeLogin(state, msg) {
      state.loginIsShow = msg;
    },
    login(state, msg) {
      if (sessionStorage.getItem('userData')) {
        state.isLogin = true
      } else {
        state.isLogin = msg
      }
    },
    loading(state, msg) {
      state.isLoding = msg
    },
    addPostData(state, { page, limit }) {
      state.pages = page
      state.limits = limit
    }
  },
  actions: {

  }
})
